const getUserInitials = (fullName: string | null | undefined) => {
  if (!fullName || !fullName.trim()) {
    return "?"
  }

  return fullName
    .trim()
    .split(" ")
    .map(name => name.trim().charAt(0).toUpperCase())
    .filter(letters => letters)
    .slice(0, 2)
    .join("")
}

export { getUserInitials }
